html,
body,
button {
  font-family: "Quicksand", sans-serif;
  color: #80554d;
}
/* use */

main {
  position: relative;
  z-index: 10;
}

.placeholder {
  height: 20px;
  font-size: smaller;
  text-align: center;
}

.parallax {
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  perspective: 2px;
  transform-style: preserve-3d;
}

.section {
  position: relative;
  height: 100vh;
  overflow-y: hidden;
}

.content {
  height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
}

.container {
  margin: 0, auto;
  left: 0;
  right: 0;
  padding: 50px;
}

.content_box {
  height: 80vh;
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
}

.testimonial-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  /* align-items: center; */
  align-content: center;
}

.link-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  /* align-items: center; */
  align-content: center;
}

.testimonial-1 {
  background-color: #e4ccc7;
  border-radius: 10px 0 0 10px;
}

.testimonial-2 {
  background-color: #c5c2c1;
  border-radius: 0 10px 10px 0;
}

.testimonial-3 {
  background-color: #b19db7;
}
.box-1 div,
.box-2 div {
  /* border: 19px #faf9fe solid; */
  padding: 10px 15px;
  margin: 2px;
}

.box_1,
.box_2,
.box_3,
.box_4 {
  flex: 1 1;
}

.parallax::after {
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  transform: translateZ(-1px) scale(1.5);
  background-size: 100%;
  z-index: -1;
}

/* The styling for the static div. */
.static {
  background: #fee9e5;
  /* z-index: 1; */
}

.stripe {
  color: white;
  background: repeating-linear-gradient(
    -55deg,
    #fee9e5,
    #fee9e5 10px,
    #fff4f2 10px,
    #fff4f2 20px
  );
}

.bg1::after {
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(/the-chronologie/static/media/001.0fb3c17c.jpg);
}

/* TEXT STYLES */
.title {
  font-family: "Sacramento", cursive;
  font-weight: normal;
  font-size: 290px !important;
  color: #62505b;
}
.heading {
  font-family: "Sacramento", cursive;
  font-weight: normal;
  font-size: 65px !important;
  color: #80554d;
}

p {
  /* font-size: 20px; */
}

.text-white {
  color: #faf9fe;
}

.ls {
  margin: 0, auto;
  letter-spacing: 4px;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 33%;
  text-align: center;
}

.description {
  font-family: "Roboto Slab", serif !important;
  font-weight: normal;
  font-size: 14px;
}

.text-center {
  text-align: center;
}

.progress {
  font-family: "Roboto Slab", serif !important;
  font-size: 52px;
  font-weight: bolder;
  margin: -10px 0 20px 0;
}

/*custom button*/
.btn-more {
  border: 5px solid #faf9fe;
  border-radius: 8px;
  background: #faf9fe;
  color: #3a3d42;
  padding: 24px 130px;
  font-size: 14px;
  text-decoration: none;
  font-weight: bold;
  transition: background-color 0.1s ease, color 0.1s ease;
  cursor: pointer;
}

.btn-more:hover,
.btn-more:active {
  background-color: #3a3d42;
  color: #faf9fe;
}

/* On screens that are 992px wide or less, go from four columns to two columns */
@media screen and (max-width: 992px) {
  .column {
    width: 50%;
  }
  .title {
    font-size: 200px !important;
  }
  .ls {
    font-size: 13px !important;
  }
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  p,
  h3,
  footer {
    font-size: smaller;
  }
  .container {
    padding: 0;
  }
  .box-1 div,
  .box-2 div {
    padding: 3px 8px;
    margin: 2px;
  }
  .title {
    font-size: 100px !important;
  }
  .ls {
    font-size: 13px !important;
  }
  .progress {
    font-size: 43px;
    margin: -14px 0;
  }
  .testimonial-container {
    flex-direction: column;
  }
  .testimonial-1,
  .testimonial-2 {
    border-radius: 10px;
  }
  .btn-more {
    padding: 14px 95px;
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (max-width: 768px) {
  .testimonial-container {
    flex-direction: column;
  }
  .testimonial-1,
  .testimonial-2 {
    border-radius: 10px;
  }
}

